@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

legend {
  padding-left: 0;
  padding-right: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  font-family: "Arial", sans-serif;
  min-width: 320px;
  height: 100%;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__body {
  margin: 0;
  min-height: 100%;
  color: #000000;
  background-color: #ffffff;
}

.page-header__logo {
  grid-column-start: 1;
  grid-column-end: 3;
}

.main-nav {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  background-color: #ffffff;
  color: #000000;
}
@media (max-width: 767px) {
  .main-nav {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (min-width: 768px) {
  .main-nav {
    width: 708px;
    margin: 0 auto;
    padding: 24px 0;
    display: grid;
    grid-template-columns: 174px 1fr;
    gap: 16px;
  }
}
@media (min-width: 1300px) {
  .main-nav {
    width: 1220px;
  }
}

@media (max-width: 767px) {
  .main-nav__bar {
    width: 320px;
    margin: 0 auto;
    padding: 12px 20px 13px;
    display: grid;
    grid-template-columns: 34px 1fr 34px;
    gap: 16px;
    align-items: center;
  }
}

.main-nav__toggle {
  border: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  position: relative;
  display: block;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle::before {
  position: absolute;
  top: 12px;
  right: 12px;
  left: 12px;
  content: "";
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-nav--closed .main-nav__toggle::before, .main-nav--closed .main-nav__toggle::after {
  position: absolute;
  top: 12px;
  right: 12px;
  left: 12px;
  content: "";
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.main-nav--closed .main-nav__toggle::after {
  transform: rotateZ(45deg);
}
.main-nav--closed .main-nav__toggle::before {
  transform: rotateZ(-45deg);
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}

.main-nav__link {
  text-decoration: none;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
  }
}
@media (min-width: 1300px) {
  .main-nav__link {
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
  }
}

