/// тут еще много вопросов как стыковать картинки.
///
///
/// кнопки "было" "стало" стилизованны в файле switcher.scss
///


.example__images {
  margin-bottom: 15px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr min-content;

  @media (min-width: $tablet-width) {
    padding: 0 58px;
  }
  @media (min-width: $desktop-width) {
    padding: 0;
    width: 592px;
  }
}

.example__image-wrapper {
  overflow: hidden; 
  //скрывает содержимое, выходящее за пределы контейнера.
  &:last-child {
    display: none; 
    // равняем картинку внутри по правому краю так ширина обертки будет меняться 
    justify-content: end;
    // задаем отступ для картинки так как она меньше чем 280px при высоте 232рх
    width: 140px;
    @media (min-width: $tablet-width) {
      display: flex;
      width: 296px;
    }
  }
}

.example__image {
  height: 232px;
  @media (min-width: $tablet-width) {
    height: 511px;
  }
}

.example__image--before {
  min-width: 280px;
  max-width: 280px;
  margin-left: 10px;
  @media (min-width: $tablet-width) {
    margin-left: 50px;
    min-width: 536px;
    max-width: 536px;
  }
}

.example__image--after {
  min-width: 243px;
  max-width: 243px;
  margin-right: 10px; 
  @media (min-width: $tablet-width) {
    margin-right: 0;
    min-width: 536px;
    max-width: 536px;
  }
}